.service-hero {
  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 70px 0 0;
    @include screen(tablet) {
      padding: 100px 0 0;
      min-height: calc(100dvh - 212px);
    }
    h1 {
      br {
        display: none;
        @include screen(laptop) {
          display: block;
        }
      }
    }
    p {
      margin-top: 24px;
      max-width: 700px;
      @include font-p0;
      br {
        display: none;
        @include screen(laptop) {
          display: block;
        }
      }
    }
  }
}